import React from 'react'
import img from './myimg.jpg'
export default function Home() {
    return (
        <div id="home">
            <div className="container text-center" style={{display:'flex' ,flexDirection:'column', alignItems:'center' ,justifyContent:'center' , height:'100vh'}}>

            <img src={img} className="rounded-circle img-fluid my-5 mt-15" alt="Cinque Terre" style={{height:'400px', width:'400px', border:'2px solid black', boxShadow:'2px 2px 2px black', borderRadius: '10px',display:'flex' ,justifyContent: 'center', alignItems:'center' }}/>
            <h2 style={{borderBottom:'2px solid black' , display:'inline'}}>Pratham Bhasin</h2>
            <h4>Engineer || Developer</h4>
            </div>

        </div>
    )
}
