import React from 'react'

export default function VideoGallery() {
    let styleMe ={
                    height:'250px',
                    width:'300px',
                    
                    border:'2px solid black',
                    boxShadow:'2px 2px 2px black',
                    borderRadius:'23px',
                    padding:'5px',
                    margin:'15px'

                }
    return (
        <div id="videoGallery" className="container">
            <h1 style={{ fontFamily: 'serif', fontStyle: 'italic', paddingTop: '10px', textAlign: 'center', paddingLeft: 'auto', fontWeight: 'bolder', fontSize: '2.5rem' }}>Video Gallery</h1>

            <hr />


            {/* <div className="container embed-responsive embed-responsive-4by3" style={{}}>
            
            </div> */}

            <div className="embed-responsive embed-responsive-16by9 container" style={{justifyContent:'center' ,alignItems:'center' ,display:'flex',flexWrap:'wrap' }}> 
  <iframe title="video-1" className="embed-responsive-item" style={styleMe}  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture  "     src="https://www.youtube.com/embed/bzIZl3tyM80"></iframe>
  <iframe title="video-2" className="embed-responsive-item" style={styleMe}  allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture  "    src="https://www.youtube.com/embed/l5lsJTKW_JY"></iframe>
  <iframe title="video-3" className="embed-responsive-item" style={styleMe} allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture  "    src="https://www.youtube.com/embed/tb-4Ftv9j3o"></iframe>
  <iframe title="video-4" className="embed-responsive-item" style={styleMe} allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture  "   src="https://www.youtube.com/embed/I39ynH3AHkM"></iframe>
  <iframe title="video-5" className="embed-responsive-item" style={styleMe} allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture  "    src="https://www.youtube.com/embed/DSCgKjW1Q1U"></iframe>
  <iframe title="video-6" className="embed-responsive-item" style={styleMe} allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture  "    src="https://www.youtube.com/embed/2Tp0_BHkYug"></iframe>
</div>

        </div>
    )
}
