import React from 'react'
import project1 from './textUtils.png'
import project2 from './My-Website(Desktop).png'
import project3 from './liveCovidData.png'

export default function Projects() {
    return (
        <div id="projects" className="container" style={{ backgroundColor: '#dcdcde', borderRadius: '20px', height: 'auto', paddingBottom: '50px' , marginBottom:'30px'}}>
            <h1 style={{ fontFamily: 'serif', fontStyle: 'italic', paddingTop: '10px', textAlign: 'center', paddingLeft: 'auto', fontWeight: 'bolder', fontSize: '2.5rem' }}>Projects</h1>

            <hr />
<div className="container" style={{textAlign:'center'}}>

            <div className="card" >
                <img className="card-img-top" src={project1} alt="ProjectImg" />
                <div className="card-body">
                    <h2 className="card-title">Text-Utils </h2>
                    <p className="card-text" style={{fontSize:'1.3rem'}}>Text-Utils is a react based application which can be used to customize your text the way you want.</p>
                    <a href="https://prathambhasin.github.io/My-TextUtils/" target="_blank" className="btn btn-primary" rel="noreferrer">Visit Project</a>
                </div>
            </div>
<br />

            <div className="card" >
                <img className="card-img-top" src={project2} alt="ProjectImg" />
                <div className="card-body">
                    <h2 className="card-title">My-Website (Desktop Version)</h2>
                    <p className="card-text" style={{fontSize:'1.3rem'}}>This is the Desktop Version Of My Website made by HTML , CSS ,Javascript . Please use Desktop to Visit this site for Better View.</p>
                    <a href="https://prathambhasin10.000webhostapp.com/index.html" target="_blank" className="btn btn-primary" rel="noreferrer">Visit Project</a>
                </div>
            </div>

<br />
            <div className="card" >
                <img className="card-img-top" src={project3} alt="ProjectImg" />
                <div className="card-body">
                    <h2 className="card-title">Live Covid Tracker</h2>
                    <p className="card-text" style={{fontSize:'1.3rem'}}>This is a live Covid Data Tracker which fetch data from api and display it in form of table and a map.</p>
                    <a href="https://livecoronadata.000webhostapp.com/" target="_blank" className="btn btn-primary" rel="noreferrer">Visit Project</a>
                </div>
            </div>
            <br />
</div>
        </div>
    )
}
